  .carousel-info {
    bottom: 20px;
    width: 100% !important;
    top: unset;
  }

  .carousel-title {
    width: 100%;
  }

  .section-main-product-wrapper {
    flex-direction: column;
  }

  .main-product-image {
    width: 100%;
  }

  .main-product-info {
    margin-top: 35px;
    width: 100%;
    padding-left: 0;
  }

  .collection-main-left-sidebar {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    background: rgba(0, 0, 0, .2);
  }

  .product-actions {
    visibility: visible;
    transform: unset;
    text-align: right;
    bottom: 5px;
    left: -5px;
  }

  .cart-item-info div {
    padding: 8px;
  }

  .product-actions .add-to-cart-btn {
    width: 36px;
    height: 36px;
    padding: 0 5px;
    line-height: 30px;
    color: #3d4246;
    font-weight: 400;
    background: rgba(34, 34, 34, .5);
    border: none;
  }

  .add-to-cart-btn span {
    display: none;
  }

  .add-to-cart-btn svg {
    width: 12px;
    display: inline-block !important;
    fill: #fff;
    height: auto;
    vertical-align: middle;
  }

  .filter-wrapper {
    background-color: white;
    position: absolute;
    height: 100%;
    right: 0;
    width: calc(100% - 60px) !important;
  }

  .section-toolbar {
    padding: 0;
  }

  .toolbar-mid-side {
    display: none;
  }

  .toolbar-left-side {
    text-align: center;
    width: 50%;
    padding: 10px;
    border-right: 1px solid #eee;
  }

  .toolbar-right-side {
    width: 50%;
    padding: 10px;
    text-align: center;
  }

  .section-collection-main {
    padding-left: 0;
  }

  .collection-description {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-align: unset;
  }

  .sort-container {
    width: 100%;
    top: calc(100% + 10px);
  }

  .cart-template .cart-item {
    grid-template-columns: 2fr 1fr;
    margin: 0;
    padding-bottom: 15px;
    border-top: none;
  }

  .cart-list-items {
    margin: 0 15px;
  }

  .cart-item {
    padding-top: 10px;
  }

  .cart-item-info-title {
    color: #3d4246;
    font-size: 15px;
    font-weight: bold;
  }

  .cart-item-quantity {
    grid-column: 2;
  }

  .cart-item-total,
  .cart-list-heading {
    display: none !important;
  }

  .cart-item-product {
    grid-row-start: 1;
    grid-row-end: 3;
  }

  .cartlist {
    padding: 40px 0;
  }

  .cart-template .item-image {
    width: 92px;
  }

  .item-info,
  .cart-item-price {
    font-weight: 600;
    color: black;
  }

  .banner-info {
    bottom: 0px;
  }

  .banner-info-title h3 {
    font-size: 20px;
    margin: 0;
  }

  .banner-info-text p {
    font-size: 16px;
  }

  .banner-item {
    margin-bottom: 24px;
  }

  .carousel-image {
    height: unset;
  }

  .carousel-title {
    font-size: 20px;
  }

  .section-header {
    padding: 25px 0;
  }

  .section-home {
    padding: 0 32px;
  }

  /* .section-home .section-footer {
    margin: 0 -40px;
  } */
  .navigation-item:first-child {
    padding-left: 0;
  }

  .section-header {
    padding: 25px 16px;
  }

  .section-main-product-wrapper {
    padding: 20px 16px;
  }

  .main-product-actions {
    flex-wrap: wrap;
  }

  .main-quickview-btn {
    margin-bottom: 20px !important;
  }

  .section-home .section-header {
    padding: 25px 0;
  }

  .search-tempalte .collection-main-left-sidebar {
    display: none;
  }

  .search-tempalte .collection-main-right-side {
    width: 100%;
  }

  .header-right {
    position: relative;
  }

  .gg-login-btn,
  .logout-btn {
    z-index: 9;
    position: absolute;
    top: 100%;
    display: flex;
    left: 0;
    width: 100%;
    justify-content: right;
  }

  .totalPrice {
    padding: 15px;
    text-align: right;
    margin-bottom: 15px;
  }
}